<template>
  <div class="SPoints">
    <div class="pd">
      <p class="tl">¿Dónde nos encuentras?</p>
      <div class="cities-c">
        <div
          class="sel"
          v-for="(c, i) in cities"
          :key="i"
          @click="click(c.city)"
          :class="{ act: selcity == c.city }"
        >
          {{ c.city }}
        </div>
      </div>
      <section v-if="selcity !== ''">
        <div class="spoints-c" v-if="selarr">
          <a v-for="(a, j) in selarr.points" :key="j" href="" class="point">
            <img :src="a.image" :alt="a.name" />
          </a>
        </div>     
      </section>
      <section v-else>
        <div class="spoints-c">
          <p class="grtl">Selecciona la ciudad</p>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cities: [
        {
          city: "Hermosillo",
        },
        {
          city: "San Carlos",
        },
        {
          city: "Puerto Peñasco",
        },
      ],
      points: [
        {
          city: "Hermosillo",
          points: [
            {
              name: "Santovalle",
              image: require("../../assets/pro1.png"),
            },
            {
              name: "Taste",
              image: require("../../assets/pro2.png"),
            },
            {
              name: "Mandadito",
              image: require("../../assets/pro3.png"),
            },
            {
              name: "Super del Norte",
              image: require("../../assets/pro4.png"),
            },
            {
              name: "Coyotas",
              image: require("../../assets/pro5.png"),
            },
          ],
        },
        {
          city: "San Carlos",
          points: [
            {
              name: "Santovalle",
              image: require("../../assets/pro1.png"),
            }
          ],
        },
        {
          city: "Puerto Peñasco",
          points: [  
            {
              name: "Super del Norte",
              image: require("../../assets/pro4.png"),
            }, 
          ],
        },
      ],
      selcity: "",
      selarr: [],
    };
  },
  methods: {
    click(val) {
      let arr = this.points;
      this.selcity = val;
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].city == val) {
          this.selarr = arr[i];
        }
      }
    },
  },
};
</script>
<style>
.SPoints .pd {
  padding: 30.918vw 20.531vw;
}
.SPoints .tl {
  font-family: "Chunkfive";
  font-weight: 400;
  font-size: 9.662vw;
  line-height: 9.662vw;
  text-align: center;
  color: #000000;
}
.SPoints .sel {
  width: 100%;
  height: 16.425vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #d9d9d9;
  font-family: "Glory";
  font-style: normal;
  font-weight: 700;
  font-size: 4.348vw;
  line-height: 4.831vw;
  text-align: center;
  letter-spacing: 0.125em;
  text-transform: uppercase;
  margin-bottom: 8.937vw;
}
.SPoints .sel.act {
  background-color: #f70017;
  color: white;
}
.SPoints .point img {
  max-width: 45.894vw;
  min-width: 29.952vw;
  margin: 0 auto 8.937vw;
  display: flex;
  filter: grayscale(1);
  transition: .5s;
}
.SPoints .point:hover img{
  filter: grayscale(0); 
}
.SPoints .grtl {
    font-family: "Chunkfive";
    font-weight: 400;
    font-size: 9.662vw;
    line-height: 9.662vw;
    text-align: center;
    color: #bbbbbb;
    margin: 25vw 0 0;
}
@media (min-width: 768px) {
  .SPoints .pd {
    padding: 10vw 10.764vw 12.222vw;
}
.SPoints .tl {
    font-size: 3.472vw;
    line-height: 3.472vw;
    margin: 0 auto 3.542vw;
}
.cities-c {
    display: flex;
    margin: auto;
    width: 42%;
}
.SPoints .sel {
    background: transparent;
    height: auto;
    font-size: 0.972vw;
    line-height: 1.111vw;
    margin-bottom: 0;
    cursor: pointer;
    transition: .5s;
}
.SPoints .sel:hover {
    color: #f70017;
}
.SPoints .sel.act{
    background: transparent;
    color: #f70017;
}
.SPoints .spoints-c {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 6.875vw;
}
.SPoints .point img {
    max-width: 12.222vw;
    min-width: 7.222vw;
    margin: 0 auto 0;
    display: flex;
}
.SPoints .grtl { 
    font-size: 2.662vw;
    line-height: 2.662vw; 
    margin: 0;
    width: 100%;
}
}
</style>